@import url(https://fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css);
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}

body {
    line-height: 1;
}

ol, ul {
    list-style: none;
}

blockquote, q {
    quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

a:link, a:visited, a:hover, a:active {
    text-decoration: none;
}
body {
    font-family: "Noto Sans KR", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.detail-title {
    min-height: 30px;
    font-size: 23px;
    font-weight: 600;
    letter-spacing: -0.07em;
    color: #000;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-bottom: 5px;
    padding: 5px 0;
}

.detail-description {
    width: 100%;
    font-size: 17px;
    color: #666;
    min-height: 20px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-bottom: 14px;
}

.detail-box {
    display: flex;
    margin-bottom: 7px;
}

.detail-label {
    color: #777;
    font-size: 13.5px;
    margin-right: 90px;
    white-space: nowrap;
    width: 50px;
}

.detail-text {
    line-height: 17px;
    font-size: 13.5px;
    color: #363636;
}

.detail-price {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    letter-spacing: -0.03em;
    font-size: 28px;
    font-weight: 600;
    color: #000;
}

.detail-price>.detail-price-unit {
    margin-left: 5px;
    font-size: 14px;
}

.select-count {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #e2e2e2;
    border-bottom: 1px solid #e2e2e2;
    padding: 10px 0;
    font-size: 15px;
}

.select-count-stock {
    margin-left: 5px;
    font-size: 11px;
}

.select-input-wrap {
    position: relative;
    width: 70px;
    height: 30px;
}

.select-input {
    text-indent: 4px;
    width: 100%;
    height: 100%;
    outline: none;
    box-sizing: border-box;
}

.select-input-btn-wrap {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    height: 100%;
    width: 24px;
    flex-direction: column;
}

.select-input-btn-wrap button {
    position: relative;
    height: 50%;
}

.select-input-btn-wrap button svg {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
}

.amount-price {
    margin-top: 15px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    letter-spacing: -0.03em;
    font-size: 28px;
    font-weight: 600;
    color: #18C2BD;
}

.amount-price-text {
    margin-right: 15px;
    font-size: 14px;
    color: #000;
}

.amount-price-unit {
    margin-left: 5px;
    font-size: 14px;
}

.confirm-text {
    font-weight: 600;
}
.item-wrap {
    position: relative;
}

.image-wrap {
    position: relative;
}

.item-image {
    width: 215px;
    border-radius: 50%;
    margin: 0 auto;
    margin-bottom: 30px;
}

.hover-image {
    position: absolute;
    width: 215px;
    height: 215px;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    color: #fff;
    background-color: #00000080;
    border-radius: 50%;
    font-size: 24px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hover-image>.item-delivery-type {
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.item-title {
    min-height: 20px;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: -0.07em;
    color: #000;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-bottom: 5px;
}

.item-description {
    width: 100%;
    font-size: 13px;
    color: #666;
    min-height: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 15px;
}

.price-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
}

.s-price {
    margin-right: 2px;
    font-size: 26px;
    color: #2ac1bc;
    font-weight: bold;
    vertical-align: middle;
    line-height: 1;
    letter-spacing: -0.04em;
}

.price-unit {
    font-weight: 600;
    font-size: 14px;
    color: #2ac1bc;
    padding-left: 2px;
}

.n-price {
    margin: 0;
    margin-left: 10px;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    font-weight: normal;
    text-decoration: line-through;
    vertical-align: baseline;
    background: transparent;
    color: #888;
}

.item-badge {
    margin-top: 10px;
}

.badge {
    display: inline-block;
    zoom: 1;
    width: 50px;
    height: 11px;
    margin: 0 3px 0 0;
    padding: 5px 0 5px;
    line-height: 11px;
    text-align: center;
    font-size: 11px;
    color: #fff;
    background: #8ea1d3;
}

.launching-sale {
    background: #c6a8a5;
    width: 50px;
}

.event-sale {
    background: #b997cf;
    width: 62px;
}

.sold-out {
    background: #838085;
    width: 28px;
}
.arrow {
    position: absolute;
    top: 50%;
    -webkit-transform: scale(3.5);
            transform: scale(3.5);
    cursor: pointer;
}

.prevArrow {
    left: -90px;
}

.nextArrow {
    right: -90px;
}

.nextArrow svg:hover, .prevArrow svg:hover {
    color: #5a5a5a;
}
